@use '../util' as *;

html {
    font-size: 100%;
    box-sizing: border-box;
}

*, 
*::before, 
*::after {
    box-sizing: inherit;
}

body {
    margin: rem(25) ;
    padding: 0;
    font-family: var(--font-grotesk);
    background: var(--background);
    color: var(--text-color);
}