:root {
  --reaction: hsl(0, 100%, 67%);
  --reaction-bg: hsla(0, 100%, 67%, 0.1);
  --memory: hsl(39, 100%, 56%);
  --memory-bg: hsla(39, 100%, 56%, 0.1);
  --verbal: hsl(166, 100%, 37%);
  --verbal-bg: hsla(166, 100%, 37%, 0.1);
  --visual: hsl(234, 85%, 45%);
  --visual-bg: hsla(234, 85%, 45%, 0.1);
  --result-bg-start: hsl(252, 100%, 67%);
  --result-bg-end: hsl(241, 81%, 54%);
  --result-circle-start: hsla(256, 72%, 46%, 1);
  --result-circle-end: hsla(241, 72%, 46%, 0);
  --background: hsl(0, 0%, 100%);
  --result-text:hsl(0, 0%, 100%);
  --catagoty-bg: hsl(221, 100%, 96%);
  --button-hover: hsl(241, 100%, 89%);
  --button: hsl(224, 30%, 27%);
}

:root {
  --font-grotesk: "Hanken Grotesk", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 1.5625rem;
  padding: 0;
  font-family: var(--font-grotesk);
  background: var(--background);
  color: var(--text-color);
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card {
  display: flex;
  flex: auto;
  background: var(--background);
  border-radius: 25px;
  box-shadow: 10px 10px var(--catagoty-bg);
  width: min-content;
  justify-content: center;
  align-items: center;
}

.results {
  color: var(--result-text);
  background: var(--result-bg-start);
  border-radius: 1.5625rem;
  height: 25rem;
  width: 18.75rem;
  text-align: center;
}
.results--circle {
  display: flex;
  flex: content;
  background: var(--result-circle-start);
  border-radius: 3.125rem;
  width: 6.25rem;
  height: 6.25rem;
  justify-content: center;
  align-items: center;
}

.summary {
  margin: 0 1.5625rem;
  width: 18.75rem;
}
.summary--reaction {
  color: var(--reaction);
  background: var(--reaction-bg);
  height: 3.125rem;
}
.summary--memory {
  color: var(--memory);
  background: var(--memory-bg);
  height: 3.125rem;
}
.summary--verbal {
  color: var(--verbal);
  background: var(--verbal-bg);
  height: 3.125rem;
}
.summary--visual {
  color: var(--visual);
  background: var(--visual-bg);
  height: 3.125rem;
}

.catagory {
  display: flex;
  flex: auto;
  width: auto;
  border-radius: 0.625rem;
  margin-top: 1.5625rem;
  margin-bottom: 1.5625rem;
  justify-content: space-around;
  align-items: center;
}